.entry__options--overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  z-index: 970;
}

.entry__options {
  position: absolute;
  right: 5%;
  bottom: 5%;
  left: 5%;
  max-width: 440px;
  width: auto;
  min-width: 240px;
  height: 165px;
  background: var(--black1);
  box-shadow: var(--box-shadow1);
  border-radius: 8px;
  z-index: 980;
}

.entry__options--hidden {display:none;}

.entry__options--content {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: relative;
}

.entry__options--header {
  display: grid;
  grid-template-columns: 1fr 120px;
  height: 45px;
  width: 100%;
  align-items: center;
}

.entry__options-datetime {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  padding: 15px 0 0 18px;
  overflow:hidden;
  white-space:nowrap;
}

.entry__options-date,
.entry__options-time {
  color: var(--white2);
  font-weight: 400;
  width: 100%;
}

.entry__options-date {
  height: 17px;
  line-height: 17px;
  font-size: 13px;
}

.entry__options-time {
  height: 15px;
  line-height: 15px;
  font-size: 11px;
}

.entry__options--header-icons {
  display:flex;
  justify-content:space-between;
  align-items:center;
  height: 100%;
  width: 100%;
}

.entry__options-icon {
  display:flex;
  justify-content:center;
  align-items:center;
  height: 100%;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.entry__options-icon:hover::after {
  background: var(--mediumgrey1);
  border-radius: 2px;
  content: attr(data-tooltip);
  font-size: 12px;
  font-weight: 400;
  height: 25px;
  left: 50%;
  text-align:center;
  line-height: 25px;
  position: absolute;
  top: 60%;
  transform: translate(-50%, 80%);
  width: 78px;
  z-index: 1000
}

.entry__options-icon:last-child:hover::after {
  width: 48px;
}

.entry__options-icon svg {
  pointer-events:none;
  user-select:none;
}


.entry__options--body {
  height: 120px;
  display:flex;
  align-items:center;
  width: 100%;
  padding: 0 2px;
  overflow: hidden;
  flex-direction: column;
}

.entry-option-desc {
  width: 100%;
  height: 100%;
  display:flex;
  align-items:center;
  flex-direction: row;
}

.eob-icon {
  width: 40px;
  height: 100%;
  display:flex;
  justify-content:center;
  align-items:center;
  user-select:none;
  pointer-events:none;
}

.eob-title,
.eob-description,
.eob-category {
  width: 100%;
  height: 40px;
  color: var(--white2);
  font-family: var(--secondaryFont);
}

.eob-title {
  font-size: 15px;

}

.eob-description,
.eob-category {
  font-size: 12px;
  font-weight: 400;
}

.eob-description {
  max-height: 40px;
  height: 100%;
  overflow-y: auto;
}

.eob-title,
.eob-category {
  line-height: 40px;
  align-items:center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}




@media screen and (max-width: 530px){
  .entry__options--body {
    padding: 0 6px;
  }

  .eob-icon {
    width: 24px;
    height: 100%;
    display:flex;
    justify-content:center;
    align-items:center;
    user-select:none;
    pointer-events:none;
  }

  .eob-title,
  .eob-description,
  .eob-category {
    padding-left: 6px;
  }
}

@media screen and (max-width: 400px) {

  .entry__options {
    width: calc(100% - 18px);
  }

  .entry__options-datetime {
    padding: 15px 0 0 6px;
  }

  .entry__options-date {
    font-size: 12px;
  }

  .entry__options-time {
    font-size: 10px;
  }
}
